.Calculator {
  display: grid;
  grid-template: auto repeat(5, 65px) / repeat(4, 80px);
  grid-gap: 1px;
  padding: 5px;
  background: black;
}


#screen {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  grid-column: 1 / 5;
  min-height: 3.5em;
  font-family: Digital, sans-serif;
  text-align: end;
  margin: 2px 0;

  #total {
    color: orange;
    font-size: 1.6em;
    overflow-wrap: break-word;
  }

  #display {
    color: white;
    font-size: 1.7em;
  }
}

#clear {
  background: rgb(172, 57, 57);
  grid-column: 1 / 3;
}

#equals {
  background: rgb(0, 68, 102);
  grid-column: 4/5;
  grid-row: 5/ 7;
}

#zero {
  grid-column: 1 / 3;
}

.operator {
  background: rgb(102, 102, 102);
}

.number {
  background: #4d4d4d;
}

button {
  color: white;
  border: none;
  outline: none;
  font-family: 'Share Tech Mono', sans-serif;
  font-size: 1.3em;

  &:active {
    box-shadow: 0 0 2px 1px rgba(255, 255, 255, 1);
  }
}
